// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

%widget-style {
	max-width: 27%;
	border-left: 1px solid $color__border;
}

%content-area-style {
align-content: center;
padding: 20px 60px 0px 0px;
width: 73%;
}

