/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/


/* Posts and pages */

.site-content {
	margin-top: 3.1rem;
}

.our-team {
	margin-bottom: 0;
}

.blog {
	.site-content {
		display: flex;
		justify-content: center;
		width: $desktop__width;
		margin-left: auto;
		margin-right: auto;
		.content-area {
			align-content: center;
			padding: 60px 60px 0px 0px;
			img {
				width: 100%;
			}
		}
		.widget-area {
			min-width: 27%;
			border-left: 1px solid $color__border;
			i {
				margin-right: 0.5rem;
			}
		}
	}
	ul {
		padding-left: 0;
		padding-right: 0rem;
		margin-left: 0;
	}
}

//Journal Page

.home {
	.hfeed.site {
		.site-content {
			ul {
				list-style: none;
				align-content: center;
				margin-right: auto;
				margin-left: auto;
				width: $desktop__width;
				max-width: $desktop__width;
				img {
					max-height: 250px;
					max-width: 350px;
					width: auto;
				}
				h1 {
					line-height: 1.25;
					font-size: 1.3rem;
				}
				span,
				a {
					font-size: 0.75rem;
				}
			}
		}
	}
}

.journal-block-wrapper {
	margin-top: 3.75rem;
	max-width: $desktop__width;
	margin-right: auto;
	margin-left: auto;
	h2 {
		text-align: center;
		font-family: $font__main;
		font-size: 2.4rem;
		font-weight: 400;
		margin-bottom: 3rem;
		margin-top: 3.75rem;
		margin-right: auto;
		margin-left: auto;
		width: $desktop__width;
	}
	ul {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-content: center;
		padding-left: 0;
		li {
			margin: 1rem;
			align-content: center;
			border: 1px;
			border-style: solid;
			border-color: $color__border;
			max-width: 375px;
			flex: 0 1 29%;
			.journal-button {
				border: 1px solid $color__darkest;
				color: $color__darkest;
				padding: 0.75rem 1.5rem;
				bottom: 1.25rem;
				left: 0.9rem;
				position: relative;
				font-family: $font__main;
				text-transform: uppercase;
			}
			.journal-button:hover {
				background-color: $color__site-main;
				border-color: $color__site-main;
				color: $color__lightest;
			}
		}
	}
}

.journal-container {
	background-color: $color__border;
	img {
		width: 360px;
	}
}

.day-of-week {
	font-weight: 600;
}

.entry-header {
	position: relative;
	h2 {
		background-color: $color__site-main;
		color: $color__lightest;
		font-size: 33.6px;
		font-size: 2.1rem;
		position: absolute;
		left: 0;
		top: 0;
		padding: .75rem 1.5rem;
		margin-top: 2.25rem;
		max-width: 75%;
	}
	a {
		color: $color__lightest;
	}
}

.entry-meta {
	font-family: $font__main;
	text-transform: uppercase;
	background-color: $color__site-main;
	color: $color__lightest;
	display: inline-block;
	padding: 0.2rem 1.5rem;
	position: absolute;
	bottom: 8px;
	right: 0px;
}

.journal-entry-wrapper {
	padding: 0.75rem;
	h3 {
		font-size: 1.3rem;
		line-height: 1.5rem;
		color: $color__site-main;
		margin-top: 0;
		padding-bottom: 0.5rem;
	}
	h3:hover {
		color: $color__main-hover;
		cursor: pointer;
	}
	a {
		font-family: $font__main;
	}
}

.product-info.container {
	align-content: center;
	margin-left: auto;
	margin-right: auto;
	width: $desktop__width;
	h2 {
		font-size: 2.4rem;
		font-weight: 400;
		margin-bottom: 3.75rem;
		margin-top: 3.75rem;
		text-align: center;
	}
	.product-type-blocks {
		display: flex;
		justify-content: space-between;
		text-align: center;
		.product-type-block-wrapper:hover {
			border-color: $color__site-seconday-hover;
		}
		.product-type-block-wrapper {
			border: 1px solid $color__border;
			border-top-width: 5px;
			flex-grow: 0;
			flex-shrink: 1;
			flex-basis: 22.5%;
			max-width: 22.5%;
			padding: .75rem;
			img {
				height: 70px;
				width: 70px;
			}
			a {
				background-color: $color__site-main;
				border: none;
				border-radius: 0;
				color: $color__lightest;
				font-family: $font__main;
				font-size: 14.4px;
				font-size: .9rem;
				line-height: 1;
				padding: .75rem 1.5rem;
				text-decoration: none;
				text-transform: uppercase;
			}
			a:hover {
				background-color: $color__site-main-hover;
			}
			p {
				margin-bottom: 1.5rem;
				color: $color__text-main;
				font-family: $font__secondary;
				font-size: 0.96rem;
				line-height: 1.75;
			}
		}
	}
}

//Journal Pages

.read-more-button {
	border: 1px solid $color__darkest;
	color: $color__darkest;
	font-family: $font__main;
	padding: 0.75rem 1rem;
}

.read-more-button:hover {
	border: none;
	color: $color__lightest;
	background-color: $color__site-main;
	font-family: $font__main;
	padding: 0.75rem 1rem;
}

.read-more-button:visited {
	text-decoration: none;
	color: $color__darkest;
}

.read-more-button:visited:hover {
	text-decoration: none;
	color: $color__lightest;
}

//Single Journal entry page

.single-post {
	line-height: 1.5;
	.hentry {
		margin-bottom: 1rem;
	}
	.nav-links {
		display: none;
	}
	.hfeed.site {
		.site-content {
			display: flex;
			justify-content: center;
			width: $desktop__width;
			margin-left: auto;
			margin-right: auto;
			.entry-footer {
				font-family: $font__main;
				text-transform: uppercase;
				.cat-links {
					margin-right: 1.5rem;
				}
			}
			.widget-area {
				min-width: 27%;
				border-left: 1px solid $color__border;
				ul {
					margin-left: 0;
					padding-left: 0;
				}
				i {
					margin-right: 0.5rem;
				}
			}
			.content-area {
				align-content: center;
				padding: 60px 60px 0px 0px;
				width: 73%;
				.comments-area {
					.comment-list {
						margin-left: 0;
						padding-left: 0;
					}
					.comment-body {
						display: flex;
						font-family: $font__secondary;
					}
					.comment-content {
						border-bottom: 1px solid $color__border;
						margin-top: 0.5rem;
						margin-bottom: 0.5rem;
						padding: 1rem;
						.fn {
							font-style: normal;
							font-weight: 600;
						}
					}
					.comment-metadata {
						color: grey;
					}
					a {
						color: $color__site-main;
					}
					img {
						height: 64px;
						width: 64px;
						margin-right: 2rem;
						margin-top: 2rem;
						border-radius: 50%;
					}
				}
				.BodyA {
					font-family: $font__secondary;
					font-size: 16px;
					line-height: 1.75;
					color: $color__text-main;
				}
				.entry-header {
					position: relative;
					h1 {
						background-color: $color__site-main;
						color: $color__lightest;
						font-size: 33.6px;
						font-size: 2.1rem;
						position: absolute;
						left: 0;
						top: 0;
						padding: .75rem 1.5rem;
						margin-top: 2.25rem;
						max-width: 75%;
					}
					a {
						color: $color__lightest;
					}
				}
				.single-entry-meta {
					font-family: $font__main;
					text-transform: uppercase;
					background-color: $color__site-main;
					color: $color__lightest;
					display: inline-block;
					padding: 0.2rem 1.5rem;
					position: absolute;
					bottom: 6px;
					right: 0px;
				}
				.journal-entry-wrapper {
					padding: 0.75rem;
					h3 {
						font-size: 1.3rem;
						line-height: 1.5rem;
						color: $color__site-main;
						margin-top: 0;
						padding-bottom: 0.5rem;
					}
					h3:hover {
						color: $color__main-hover;
						cursor: pointer;
					}
					a {
						font-family: $font__main;
					}
				}
			}
		}
		img {
			width: 100%;
		}
	}
}

.widget-title {
	font-weight: bold;
}

.archive {
	.site-content {
		display: flex;
		width: $desktop__width;
		margin-right: auto;
		margin-left: auto;
		.content-area {
			align-content: center;
			padding: 30px 60px 0px 0px;
			width: 73%;
			img {
				width: 100%;
			}
		}
		.widget-area {
			min-width: 27%;
			border-left: 1px solid $color__border;
			ul {
				padding-left: 0;
				margin-left: 0;
				a {
					color: $color__site-main;
				}
			}
		}
	}
}

.post-62 {
	display: none;
}

.sticky {
	display: block;
}

.hentry {
	margin: 0 0 $base__space-unit;
	margin-bottom: 4.5rem;
}

.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	line-height: 1.5;
	margin: $base__space-unit 0 0;
	a {
		color: $color__darkest;
	}
}

.page-links {
	clear: both;
	margin: 0 0 $base__space-unit;
}


/* Asides */

.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
	display: none;
}


/* Comments */

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}